      <template>
        <el-button @click="printSheet" type="primary">打印</el-button>
      </template>
      <!-- 弃审 -->
      <template v-if="TableData.auditor_status == 2">
        <!-- 已审核 -->
        <el-button plain @click="audit('otherStockNopassInvalid')" size="mini" v-if="authority.b_nopass==1">弃审</el-button>
      </template>
      <!-- 未审核 -->
      <template v-if="TableData.auditor_status == 1">
        <el-button type="primary" @click="audit('otherStockAuidtorPass')" v-if="authority.b_pass==1">审核</el-button>
        <el-button @click="audit('otherStockAuditorInvalid')" v-if="authority.b_invalid==1">作废</el-button>
      </template>
      <!-- 作废 -->
      <template v-if="TableData.auditor_status == 3 || TableData.auditor_status == 2 || (authority.b_invalid != 1 && authority.b_pass != 1)">
        <el-button @click="goBack">返回</el-button>
      </template>
      <el-button @click="goBack" v-else>取消</el-button>
