<script>
import { mapActions, mapGetters } from "vuex";
import lodopDelivery from "common/components/public/PublicPrint.vue"; //其它出库单
export default {
  name: "instorageOthenoutstorageSalesoutstorageSee",
  data() {
    return {
      authority: {},
      TableData: {
        goods_info: [] //只有新增的时候需要
      },
      title: "",
      defaultArea: null, //默认的区域名
      defaultAreaId: [], //选中的区域
      allocationList: {
        options: []
      },
      checkPage: ""
    };
  },
  components: {
    lodopDelivery
  },
  computed: {
    ...mapGetters([
      "salesoutstorageSeeGet",
      "salesoutstorageSeeSearch_showGet",
      "printingStatusGet"
    ])
  },
  methods: {
    ...mapActions([
      "othenoutstorageIndexACT",
      "outstorageauditDefault_show",
      "printingStatus"
    ]),
    printSheet() {
      this.printingStatus(true);
      this.$set(this, "title", "otherOutboundSinglesToPrint");
      this.$nextTick(() => {
        this.$refs.lodopDelivery.printPdf({
          outstock_id: this.TableData.outstock_id
        });
      });
    },
    goBack() {
      if (this.checkPage == "outstorageauditPage") {
        this.outstorageauditDefault_show({
          commont: "outstorageauditDefault",
          goPage: this.salesoutstorageSeeSearch_showGet.goPage,
          params: this.salesoutstorageSeeSearch_showGet.params,
          pageSize: this.salesoutstorageSeeSearch_showGet.pageSize
        });
        this.$router.push({
          path: "/inventory/othenoutstorage"
        });
      } else {
        this.othenoutstorageIndexACT({
          goPage: this.salesoutstorageSeeGet.goPage,
          params: this.salesoutstorageSeeGet.params,
          pageSize: this.salesoutstorageSeeGet.pageSize
        });
        this.$router.push({
          path: "/inventory/othenoutstorage"
        });
      }
    },
    audit(api) {
      //审核
      let vm = this;
      if (api == "otherStockAuidtorPass") {
        // 校验对接车郎中
        this.$baseData
          .checkCarDoctor({
            bill_type:
              this.TableData.outstock_type_id === 10003
                ? 10
                : this.TableData.outstock_type_id === 10004
                ? 11
                : 12,
            bill_id: this.TableData.outstock_id,
            operation_type: 4
          })
          .then(() => {
            // 校验安全库存金额
            this.$axMethod
              .safetyStockCheck({
                bill_logogram: "qtck",
                order_type: 2,
                bill_id: this.TableData.outstock_id
              })
              .then(() => {
                this.$axMethod[api]({
                  outstock_id: this.TableData.outstock_id
                }).then(({ data }) => {
                  if (data.code == 200) {
                    let msg = data.data;
                    if (typeof msg != String && data.msg) {
                      msg = data.msg;
                    }
                    this.$message({
                      message: msg,
                      type: "success"
                    });
                    this.goBack();
                  }
                });
              });
          });
      } else {
        // 校验对接车郎中
        this.$baseData
          .checkCarDoctor({
            bill_type:
              this.TableData.outstock_type_id === 10003
                ? 10
                : this.TableData.outstock_type_id === 10004
                ? 11
                : 12,
            bill_id: this.TableData.outstock_id,

            operation_type: api == "otherStockAuditorInvalid" ? 6 : 5
          })
          .then(() => {
            this.$axMethod[api]({
              outstock_id: this.TableData.outstock_id
            }).then(({ data }) => {
              if (data.code == 200) {
                let msg = data.data;
                if (typeof msg != String && data.msg) {
                  msg = data.msg;
                }
                this.$message({
                  message: msg,
                  type: "success"
                });
                this.goBack();
              }
            });
          });
      }
    },
    initData() {
      //详情数据
      this.$axMethod
        .otherStockCheckOutstock({
          outstock_id: this.$route.query.outstock_id,
          outstock_sn: this.$route.query.outstock_sn,
        })
        .then(({ data }) => {
          if (data.code == "200") {
            this.$set(this, "TableData", data.data);
          }
        });
    },
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 1) {
          sums[index] = "总计";
          return;
        }
        const values = data.map(item => Number(item[column.property]));
        if (!values.every(value => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          if (index == 7) {
            sums[index] = parseFloat(sums[index])
              .toFixed(0)
              .toString()
              .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
          } else if (index == 9) {
            sums[index] = parseFloat(sums[index])
              .toFixed(2)
              .toString()
              .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
          } else {
            sums[index] = "";
          }
        } else {
          sums[index] = "";
        }
      });
      return sums;
    }
  },
  created() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    this.$common.options("getAllocationListOption", this.allocationList);
    this.initData()
  }
};
</script>

